


import axios from "axios";
import { message } from 'antd';

axios.defaults.baseURL = "https://tianjiangyang.top:3000/";


//     Interceptors 
//    
axios.interceptors.request.use(function (config) {
    //  
    message.info('');
    console.log(config);
    return config;
}, function (error) {
    //   
    message.error('');
    return Promise.reject(error);
});

//   
axios.interceptors.response.use(function (response) {
    //  
    console.log(response);
    message.success('');
    return response;
}, function (error) {
    message.error('');
    //   
    return Promise.reject(error);
});


export  {axios};

