import './index.scss';
import { axios } from '@/utils/axios';
import { connect } from 'react-redux';

import {changeUpdategood,clearUpdategood}  from "~/actions"
const PAGE_SIZE = 5;

import { Row, Col, Select, Input, Button, Table } from 'antd';
const { Option } = Select;


@connect(
	(state) => ({
		
	}),
	(dispatch) => ({
		changeUpdategood:(goodsList)=>dispatch(changeUpdategood(goodsList)),
		toclearUpdategood:()=>dispatch(clearUpdategood(""))
	})
)

export default class GoodsAdmin extends Component {
	state = {
		loading:false,
		goodsList:[],
		total:0,
		searchType:"productName",
		searchKey:""
	}
	//
	getGoodsList = async (pageNum) => {
		this.pageNum = pageNum
		this.setState({
			loading: true
		});
		const{searchKey,searchType} = this.state
		let result
		if(!this.isSearch){
			 result = await axios.get("/manage/product/list",{params:{
				pageNum,
				pageSize:PAGE_SIZE
			}})
		}else{
			result = await axios.get("manage/product/search",{params:{ 
				pageNum,
				pageSize:PAGE_SIZE,
				[searchType]:searchKey,
			}})
		}
		if(result.data.status ==0){
			this.setState({
				total:result.data.data.total,
				goodsList:result.data.data.list
			})
		}
		this.setState({ loading: false });
	};

	setStatus = async (_id,status)=>{
		// debugger 
		status = status==1?2:1;
		// debugger
		const result = await axios.post("/manage/product/updateStatus",{
			productId:_id,
			status,
		})
		// console.log(result)
		if(result.data.status == 0){
			this.getGoodsList(this.pageNum);
		}
	}

	componentDidMount() {
		this.getGoodsList(1);
	}

	render() {
		const {loading,total,goodsList}=this.state
		const {changeUpdategood} = this.props
		const columns = [
			{
				title: '',
				dataIndex: 'name',
				key: 'name',
				width: 250
			},
			{
				title: '',
				dataIndex: 'desc',
				key: 'desc',
				width: 800
			},
			{
				title: '',
				dataIndex: 'price',
				key: 'price',
				width: 100,
				render:(price)=>""+price
			},
			{
				title: '',
				width: 50,
				key: 'status',
				render: (goodsList) => (
					<div style={{ display: 'flex', flexDirection: 'column' }}>
						<Button style={{width:70}} type={goodsList.status == 1?"danger":"primary"} onClick={()=>this.setStatus(goodsList._id,goodsList.status)}>{goodsList.status == 1?"":"" }  </Button>
						<span>{goodsList.status == 1?"":""}</span>
					</div>
				)
			},
			{
				title: '',
				width: 50,
				key: 'action',
				render: (goodsList) => (
					<span style={{ display: 'flex', flexDirection: 'column' }}>
						<Button type="link" onClick={()=>{changeUpdategood(goodsList),this.props.history.push("/index/admin/goodsadmin/goodsdetail")}}></Button>
						<Button type="link" onClick={()=>{changeUpdategood(goodsList),this.props.history.push("/index/admin/goodsadmin/addOrUpdateGoods")}}></Button>
					</span>
				)
			}
		];
		return (
			<div className="goodsAdmin">
				<div className="content">
					<div className="contentTop">
						<Row>
							<Col span={12}>
								<Select defaultValue="productName" style={{ width: 200 }} onChange={(value)=>{this.setState({searchType:value})}} >
									<Option value="productName"></Option>
									<Option value="productDesc"></Option>
								</Select>
								<Input style={{ width: 200, marginLeft: 20, marginRight: 20 }} onChange={(event)=>{this.setState({searchKey:event.target.value})}} placeholder="" />
								<Button type="primary" onClick={()=>{this.getGoodsList(1),this.isSearch=true}}></Button>
							</Col>
							<Col span={12}>
								<Button type="primary" style={{ float: 'right' }} onClick={()=>{this.props.toclearUpdategood(),   this.props.history.push("/index/admin/goodsadmin/addOrUpdateGoods")}}>
									+ 
								</Button>
							</Col>
						</Row>
					</div>
					<div className="contentBottom">
						<Table
							rowKey="_id"
							loading={loading}
							pagination={{ defaultPageSize: PAGE_SIZE, showQuickJumper: false,total,onChange:this.getGoodsList }}
							bordered
							columns={columns}
							dataSource={goodsList}
						/>
					</div>
				</div>
			</div>
		);
	}
}
